@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

$background-image: url('../res/images/background.jpg');
$background-color: rgb(250, 250, 250);
$main-color: rgba(230, 230, 230, 0.8);
$inverted-main-color: rgb(20, 20, 20);
$main-element-color: rgba(0, 0, 0, 0.05);
$additional-color: rgb(30, 130, 105);
$main-text-color: rgb(0, 0, 0);
$title-text-color: rgb(30, 30, 30);
$subtitle-text-color: rgb(70, 70, 70);
$description-text-color: rgb(110, 110, 110);
$link-text-color: rgb(0, 150, 255);
$main-font-family: 'Raleway', sans-serif;
$secondary-font-family: 'Kanit', sans-serif;
$additional-font-family: 'Franklin Gothic Medium', sans-serif;
$title-font-size: 22px;
$wide-font-size: 20px;
$subtitle-font-size: 19px;
$description-font-size: 16px;
$small-font-size: 14px;
$tiny-font-size: 12px;
$wide-font-weight: 900;
$bold-font-weight: 700;
$main-font-weight: 500;
$thin-font-weight: 300;
$tiny-font-weight: 100;

body {
  margin: 0;
  padding: 0;
  background-color: $background-color;
  user-select: none;
}

a {
  text-decoration: none;
}

input {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  padding: 12px;
  color: $main-text-color;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $secondary-font-family;
  background-color: $main-element-color;
  outline: none;
  border: none;
  border-radius: 12px;
}

select {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  padding: 12px;
  color: $subtitle-text-color;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $secondary-font-family;
  background-color: $main-element-color;
  outline: none;
  border: none;
  border-radius: 12px;
}

option {
  color: black;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $main-font-family;
  background-color: $main-element-color;
}

.button {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 12px 6px;
  padding: 12px;
  color: $main-text-color;
  font-size: $description-font-size;
  font-weight: $bold-font-weight;
  font-family: $main-font-family;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  transition: 0.1s;

  &:active {
    transform: scale(95%);
    filter: brightness(60%);
    transition: 0.05s ease;
  }
}

.text-inverted {
  color: $background-color;
}

#root {
  overflow-x: hidden;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  position: relative;
  height: 100%;
  width: 100%;
  background-color: $background-color;
}

.section {
  overflow-x: hidden;
  overflow-y: scroll;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  align-items: center;
  justify-content: center;
  gap: 12px 0;
  padding: 12px 12px 6px 12px;
  position: relative;
  height: 100%;
  width: 100%;
  background-color: transparent;
  backdrop-filter: brightness(100%);
}